import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { EditPackageModuleComponent } from './edit-package-module.component';

const routes: Routes = [{ path: '', component: EditPackageModuleComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EditPackageModuleRoutingModule { }



