import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { EditPackageModuleComponent } from './edit-package-module.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';
import { EditPackageModuleRoutingModule } from './edit-package-module-routing.module';


@NgModule({
  declarations: [EditPackageModuleComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    CoreModule,
    EditPackageModuleRoutingModule,
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]

})
export class EditPackageModuleModule { }



